import { Component, Input, OnInit } from '@angular/core';
import { Utils } from '../../utils';
import { News, NewsService } from '../news.service';
import { Router } from '@angular/router';

import {ArtistTag, ArtistTagService} from '../artisttagnames.service';
@Component({

    selector: 'app-news-list',
    templateUrl: 'news-list.component.html',
    styleUrls: ['news-list.component.css'],
    providers: [ArtistTagService], 

})
export class NewsListComponent implements OnInit {
    @Input() count;
    @Input() cols;
    @Input() detailPath: string = "/news";
    @Input() category: string;
    @Input() customClass: string;
    @Input() minimal: boolean = false;
    @Input() headerText: string;
    @Input() viewMore: boolean = false;
    @Input() featured: boolean = false;
    private noData: boolean = true;
    private newsMobileCarousel: boolean = false;
    public artistCategory: String;
    public dropDown: boolean = false;
    @Input() switchCarouselTemplate: number = 0;
    public artisttag:ArtistTag[];
    public news: News[];
    public selectedArtist: News;
    public selectedNews: News;
    public moreNews: number = 0;
    @Input() field_artist_name: String = "";
    public currentDropDownValue: String = "";
    private mobilereolution: boolean = false;
    private desktopResolution: boolean = false;

    constructor(private _artisttags: ArtistTagService ,private _newsService: NewsService, private _router: Router,) { }

    getNews() {
        this.selectedNews = undefined;
        this.selectedArtist = undefined;
        this.news = [];

        this._newsService.getNews(this.category, this.featured).subscribe(
            data => {
                this.news = data;
            },
            err => console.error(err),
            () => console.log('done')
        );

        return this.news;
    }

    gotoDetail(news: News) {
        let id: string;
        this.selectedNews = news;
        id = Utils.getSlug(this.selectedNews.path);
        let link = ['/news', id];
        this._router.navigate(link);
    }

    fetchNextPage() {
        this.moreNews = this.moreNews + this.count;
        this._newsService.loadmoreNews(this.moreNews, this.category).subscribe(
            data => {
                this.news = this.news.concat(data);
                if (data.length == 0 || data.length < (this.count + 1)) {
                    this.noData = false
                }
            },
            err => console.error(err),
            () => console.log('done')
        );
    }

    ngOnInit() {
        this.getArtists();
        if (window.screen.width < 767) {
            this.newsMobileCarousel = true;
        }
        this.news = this.getNews();
        if (!this.cols) {
            this.cols = "grid-4_xs-1"; //Have this on the build.
        }
        if (this.featured) {
            this.news = this.getNews();
            
        }
        if (window.screen.width < 767) {
            this.mobilereolution = true;
            this.desktopResolution = false;
        }
        if (window.screen.width > 767) {
            this.desktopResolution = true;
            this.mobilereolution = false;
        }

    }

    onResize(event) {
        if (event.target.innerWidth < 767) {
            this.newsMobileCarousel = true;
            this.mobilereolution = true;
            this.desktopResolution = false;
        } else {
            this.newsMobileCarousel = false;
            this.desktopResolution = true;
            this.mobilereolution = false;
        }
    }
    artistDropdown() {
        if (this.dropDown) {
            this.dropDown = false;
        }
        else {
            this.dropDown = true;
        }

    }
    getArtists() {
        this.artisttag = [];

        this._artisttags.getArtistTags().subscribe(
            data => {
                this.artisttag = data;
                
            },
            err => console.error(err),
            () => {

            }
        );

    }
    artistDropdownVlaue(title: String) {
        this.currentDropDownValue = title;
        this.dropDown = false;        
    }

}



