import { Component, EventEmitter, OnInit, OnChanges, ViewChild, Input, Output, ElementRef, AfterViewChecked, AfterContentInit, AfterContentChecked } from '@angular/core';
import { CarouselConfig } from '../../carousel/carousel-config';
import { CarouselComponent } from '../../carousel/carousel.component';
import { News, NewsService } from '../news.service';
import { Utils } from '../../utils';
import { IntervalObservable } from 'rxjs/observable/IntervalObservable';
import {Observable} from 'rxjs/Rx';
@Component({
  selector: 'news-img-carousel',
  templateUrl: './news-img-carousel.component.html',
  styleUrls: ['./news-img-carousel.component.css']
  
  
})
export class NewsImgComponent extends CarouselComponent implements OnInit {

  @Input() artistImages: String[];
  public carouselimageURL: String = "";
  @Input() carouseln: number;

  constructor(element: ElementRef) {
    super();
    this.node = element;
    this.carouselLength = 2;
    this.newspagecarousel=true;
    this.options = {
      itemCount: 1,
      showDots: true,
      showNav: false,
      animationSpeed: 0.5,
      autoplay: false,
      loop: true,
      responsive: {
        "767": 1,
        "1200": 1,
        "1400": 1,
        "2700": 1
      }
    };

  }

  ngOnInit() {
 
  }
  ngOnChanges() {
   
 setInterval(() => {
      this.loopthecarousel();
    }, 4000);
    
    this.carouselLength = this.carouseln;
    for (let index in this.artistImages) {
      this.artistImages[index] = Utils.getImageSrc(this.artistImages[index].toString());
      this.carouselimageURL =   this.artistImages[index];
      
    }
    if (this.artistImages.length == this.carouselLength) {
     
      this.duplicate =this.artistImages.slice();
     
      this.init();
      this.createCarousel = true;
    }
    else {

    }
  }
  loopthecarousel(){
    this.nextItem();
  }


}






