import { Injectable } from '@angular/core';
import { Http, Response, Jsonp } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { Utils } from '../utils'
import { environment } from '../../environments/environment';

export interface Artist {
  title: String;
  field_banner_image: String;
  path: String;
  field_bio: String;
  field_artist_datasource: String;
  field_artist_label_id: String;
  body: String;
  field_artist_newsletter_id: String;
  nid: String;
  field_type: String;
  field_artist_bit_id: String;
  field_main_image: String;
  field_site_link: string;
  field_twitter: String;
  field_facebook: String;
  field_youtube: String;
  field_spotify: String;
  field_instagram: String;
  field_itunes_id: String;
  field_artist_merch_url: String;
  field_home_town: String;
  field_label: String;
  field_artist_spotify_id: String;
  field_mobile_header_image: String;
}

@Injectable()
export class ArtistService {

  constructor(private http: Http) { }

  getArtists() {
    var url: string = environment.host + environment.artistListingEndpoint;

    //var url: string =  environment.host + environment.artistListingTempEndpoint;

    return this.http.get(url)

      .map((res: Response) => res.json())
      .catch(this.handleError);
  }
  getArtistsgridView() {
    var url: string = environment.host + environment.artistGridEndPoint;

    //var url: string =  environment.host + environment.artistListingTempEndpoint;

    return this.http.get(url)

      .map((res: Response) => res.json())
      .catch(this.handleError);
  }
  getArtistDetail(id: String) {
    var nid: String = Utils.getTitle(id);
    var url: string = environment.host + environment.artistDetailEndpoint;
    var theurl: string = url + '?title=' + nid;
    
    return this.http.get(url + '?title=' + nid)
      .map((res: Response) => res.json())
      .catch(this.handleError);

  }
  getArtistIdList(categoryName: string): Observable<Artist[]> {
    let url: string
    url = environment.host + environment.artistNodeIdList;

    return this.http.get(url)
      .map((res: Response) => res.json())
      .catch(this.handleError);
  }
  private handleError(error: any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg = error.message || 'Server error';
    console.error(errMsg); // log to console instead
    return Observable.throw(errMsg);
  }

}



