import { Component, OnInit, Input } from '@angular/core';
import { CommentsService, Comments } from './comments.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Utils } from '../utils';
import { environment } from '../../environments/environment';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css']
})
export class CommentsComponent implements OnInit {
  public comments: Comments[];
  private safeHtml: any;
  @Input() id: string;
  comment: string = "";
  private token: string;
  private userId: string;
  private userImage: string;
  public isMobile: boolean;

  env = environment;

  constructor(private _commentsService: CommentsService, private sanitizer: DomSanitizer) { }

  getComments() {
    this._commentsService.getComments(this.id).subscribe(
      data => {
        for (let index in data) {
          data[index].user_picture = Utils.getImageSrc(data[index].user_picture.toString());
        };
        if (window.matchMedia('(max-width: 768px)').matches) {
          this.comments = data.reverse();
        } else {
          this.comments = data;
        }
      },
      err => console.error(err),
      () => console.log('done')
    )
  }

  generateToken() {
    this._commentsService.generateToken().subscribe(
      data => {
        this.token = data;
      },
      err => console.error(err),
      () => console.log('done')
    )
  }

  deleteComment(cid) {
    this._commentsService.deleteComment(cid, this.token).subscribe(
      data => {
        this.getComments();
      },
      err => console.error(err),
      () => console.log('done')
    )
  }
  replyComment(comment, cid) {
    if (comment) {
      let commentObj = {
        "_links": {
          "type": {
            "href": ""
          }
        },
        "entity_id": [{ "target_id": "" }],
        "pid": [{ "target_id": "" }],
        "langcode": [{ "value": "en" }],
        "subject": [{ "value": "" }],
        "uid": [{ "target_id": "" }],
        "status": [{ "value": 1 }],
        "entity_type": [{ "value": "node" }],
        "comment_type": [{ "target_id": "comment" }],
        "field_name": [{ "value": "field_comment" }],
        "comment_body": [{ "value": "", "format": "script" }]
      };
      commentObj.comment_body[0].value = comment;
      commentObj.pid[0].target_id = cid;
      commentObj.entity_id[0].target_id = Utils.getNID(this.id);
      commentObj.uid[0].target_id = this.userId;

      this.comment = "";

      this._commentsService.createComment(commentObj, this.token).subscribe(
        data => {
          console.log(JSON.stringify(data));
          this.getComments();
        },
        err => console.error(err),
        () => console.log('done')
      )
    }
  }

  postComment(comment) {
    let commentObj = {
      "_links": {
        "type": {
          "href": ""
        }
      },
      "entity_id": [{ "target_id": "" }],
      "langcode": [{ "value": "en" }],
      "subject": [{ "value": "" }],
      "uid": [{ "target_id": "" }],
      "status": [{ "value": 1 }],
      "entity_type": [{ "value": "node" }],
      "comment_type": [{ "target_id": "comment" }],
      "field_name": [{ "value": "field_comment" }],
      "comment_body": [{ "value": "", "format": "script" }]
    };
    commentObj.comment_body[0].value = comment;
    commentObj.entity_id[0].target_id = Utils.getNID(this.id);
    commentObj.uid[0].target_id = this.userId;

    this.comment = "";

    this._commentsService.createComment(commentObj, this.token).subscribe(
      data => {
        console.log(JSON.stringify(data));
        this.getComments();
      },
      err => console.error(err),
      () => console.log('done')
    )
  }

  getUserDetails() {
    this._commentsService.getLoggedUser().subscribe(
      data => {
        this.userId = data[0].uid;
        this.userImage = Utils.getImageSrc(data[0].field_user_picture.toString());
      },
      err => console.error(err),
      () => console.log('done')
    )
  }

  ngOnInit() {
    this.getComments();
    this.generateToken();
    this.getUserDetails();
    if (window.matchMedia('(max-width: 768px)').matches) {
      this.isMobile = true;
    }
  }

  ngOnChanges() {
    this.getComments();
  }

}



