import { Component,OnChanges, Input, ElementRef, OnInit, HostBinding, HostListener, Renderer } from '@angular/core';
import { Artist, ArtistService } from "../artists.services";
import { Utils } from '../../utils';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { Inject } from "@angular/core";
import { DOCUMENT } from '@angular/platform-browser';
import { PageScrollConfig } from 'ng2-page-scroll';

@Component({
    selector: 'app-artistblock',
    templateUrl: './artist-block.component.html',
    styleUrls: ['./artist-block.component.css']
})
export class ArtistblockComponent implements OnInit {

    public artists: Artist[];
    public selectedArtist: Artist;
    node: ElementRef = null;
    @Input() count;
    @Input() cols;
    @Input() customClass: string;
    @Input() feildtype: String = "All";
    @Input() listView: Boolean = false;
    @Input() DetaillistView: Boolean = false;
    @Input() carouselview: Boolean = true;
    @Input() currentArtistName: string; private mouseIn: boolean = false;


    constructor( @Inject(DOCUMENT) private document: Document, private _artistService: ArtistService, private _router: Router, private elRef: ElementRef, private renderer: Renderer) {
        PageScrollConfig.defaultScrollOffset = -50;
        PageScrollConfig.defaultEasingLogic = {
            ease: (t: number, b: number, c: number, d: number): number => {
                // easeInOutExpo easing
                if (t === 0) return b;
                if (t === d) return b + c;
                if ((t /= d / 2) < 1) return c / 2 * Math.pow(2, 10 * (t - 1)) + b;
                return c / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
            }
        };
    }



    //   @HostListener('mouseenter') onMouseEnter() {
    //    this.elRef.nativeElement.querySelectorAll(".List.view li.col.List.testclass").class = 'active';
    //      this.renderer.setElementStyle(this.elRef.nativeElement.querySelectorAll(".List.view li.col.List.testclass"), 'visibility', "hidden")
    //   }

    //   @HostListener('mouseleave') onMouseLeave() {
    //     this.elRef.nativeElement.querySelectorAll(".List.view li.col.List.testclass").class = 'not-active';
    //   }
    backtotop() {
        this.document.body.scrollTop = 0;
    }
    getArtists() {
        this.selectedArtist = undefined;
        this.artists = [];
        if (this.listView) {
            this._artistService.getArtists().subscribe(
                data => {
                    this.artists = data;
                },
                err => console.error(err),
                () => console.log('done')
            );
        }
        else {
            this._artistService.getArtistsgridView().subscribe(
                data => {
                    this.artists = data;
                },
                err => console.error(err),
                () => console.log('done')
            );

        }
        return this.artists;
    }

    gotoArtistDetail(artist: Artist) {
        let id: string;
        this.selectedArtist = artist;
        id = Utils.getSlug(this.selectedArtist.path)
        let link = ['/artist', id];
        this._router.navigate(link);
    }
    ngOnInit() {
        this.artists = this.getArtists();
        // window.scroll(0, 0);  
    }
    ngOnChanges() {
        this.artists = this.getArtists();
    }

}



