import { Injectable }     from '@angular/core';
import { Http, Response } from '@angular/http';
import {JsonpModule} from '@angular/http';
import {Jsonp} from '@angular/http';
import { Headers, RequestOptions} from '@angular/http';
import { Observable }     from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

@Injectable()
export class MailingListService{
  private exactTargetSignUpURL = 'http://preview.signup.wmg.com/register?' // URL to ExactTarget 
  constructor (private jsonp: Jsonp) {}
  sendRequest (params) : Observable<any>{
   let headers = new Headers({ 'Content-Type': 'application/json' });
   var jsonObj = JSON.parse(params);
  
   function toQueryString(obj) {
    var parts = [];
    for (var i in obj) {
        if (obj.hasOwnProperty(i)) {
            parts.push(encodeURIComponent(i) + "=" + encodeURIComponent(obj[i]));
        }
    }
    return parts.join("&");
}

 var sendRequestURL = this.exactTargetSignUpURL + toQueryString(jsonObj) + '&jsonp=JSONP_CALLBACK';
   return this.jsonp.get(sendRequestURL)
                    .map(res => res.json())
                    .catch(this.handleError);
  }
  private extractData(res: Response) {
    console.log(res);
    return {};
  }
  private handleError (error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}


