import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterCountry'
})
export class FilterCountry implements PipeTransform {
  transform(value: any, args?: any): any {
    console.log("filterCountry this is args");
    console.log(args);
    let filter = args.toLocaleLowerCase();
    return filter ? value.filter(t=> t.venue_country.toLocaleLowerCase().indexOf(filter) != -1) : value;
    //return value;
  }
}



