import { Component, Input, OnInit, OnChanges ,EventEmitter, Output} from '@angular/core';
import { Utils } from '../../utils';
import { News, NewsService } from '../news.service';
import { Router } from '@angular/router';
import { LivefyreBlockComponent } from '../../livefyre/livefyre-block/livefyre-block.component';
import { PagerService } from '../../shared/pager.service';

@Component({

    selector: 'app-news-block',
    templateUrl: 'news-block.component.html',
    styleUrls: ['news-block.component.css'],
    host: {
        '(window:resize)': 'onResize($event)'
    },
    providers: [PagerService]
})

export class NewsBlockComponent implements OnInit {
    @Input() count;
    @Input() cols;
    @Input() detailPath: string = "/news";
    @Input() category: string;
    @Input() customClass: string;
    @Input() minimal: boolean = false;
    @Input() headerText: string;
    @Input() viewMore: boolean = false;
    @Input() featured: boolean = false;
    @Input() newsblock: boolean = false;
    @Input() Artistnewsblock: boolean = false;
    @Input() type: string = "news";
    @Input() ArtistNamePassed: string;

    @Input() newsIDFilter:string;
     @Output() onClose = new EventEmitter();
    private noData: boolean = true;
    private newsMobileCarousel: boolean = false;
    public artistCategory: String;
    public slicenumber: number = 1;
    public ifOnlyOneNews:boolean=false;

    pager: any = {};
    pagedItems: number = 0;
    offset: number = 0;
    totalNewsItems: number;
    firstInit: boolean = true;

    private mobilereolution: boolean = false;
    private desktopResolution: boolean = false;

    @Input() switchCarouselTemplate: number = 0;
    public featuredNews: News[];
    public news: News[];
    public selectedArtist: News;
    public selectedNews: News;
    public moreNews: number = 0;
    @Input() field_artist_name: string = "";
    public artistname: string = "";
    public nextDisable: boolean = false;

    constructor(private _newsService: NewsService, private _router: Router, private pagerService: PagerService) { }

    getNews() {

        this._newsService.getPagernews(this.offset, this.field_artist_name, this.featured).subscribe(

            data => {
                 
                this.news = data;

                if (this.firstInit || !(this.artistname == this.field_artist_name)) {
                    this.pagedItems = data.length;
                    this.setPage(1);
                    this.firstInit = false;
                    this.featuredNews = this.news;
                    this.artistname = this.field_artist_name;


                }

            },
            err => console.error(err),
            () => {
                // for (let index in this.news) {
                //     console.info(this.news[index].field_tags);
                //      this.news[index].field_tags=Utils.getroutinglink(this.news[index].field_tags);
                //      this.news[index].field_tags.toString;
                //      console.info( this.news[index].field_tags.toString);
                //  }
                //console.info("This nid done"+this.news.length);
               // console.log('done');
                if(this.news.length==1){
                    this.ifOnlyOneNews=true;
                }
                else{
                    this.ifOnlyOneNews=false;
                }
            }
        );

        return this.news;
    }

    gotoDetail(news: News) {
        this.onClose.emit(true);
        let id: string;
        this.selectedNews = news;
        id = Utils.getSlug(this.selectedNews.path);
        let link = ['/news', id];
        this._router.navigate(link);
    }

    fetchNextPage() {
        this.moreNews = this.moreNews + this.count;
        this._newsService.loadmoreNews(this.moreNews, this.category).subscribe(
            data => {
                this.news = this.news.concat(data);
                if (data.length == 0 || data.length < (this.count + 1)) {
                    this.noData = false
                }
            },
            err => console.error(err),
            () => console.log('done')
        );
    }

    ngOnInit() {
          window.scroll(0, 0);
        
        if (window.screen.width < 767) {
            this.newsMobileCarousel = true;
        }
        this.news = this.getNews();
        if (!this.cols) {
            this.cols = "grid-4_xs-1"; //Have this on the build.
        }
        if (!this.featured) {
            this.news = this.getNews();
            this._newsService.getNodeCount(this.type, this.field_artist_name).subscribe(
                data => {
                    this.getNews();
                    this.totalNewsItems = parseInt(data[0].count);

                },
                err => console.error(err),
                () => {

                }
            )
        }
        if (this.Artistnewsblock) {
           this._newsService.getArtistNews().subscribe(
               data => {
                this.news = this.news.concat(data);
                    if (data.length == 0 || data.length < (this.count + 1)) {
                        this.noData = false
                    }
                },
                err => console.error(err),
                () => console.log('done')   
            );
            
        }
        if (window.screen.width < 767) {
            this.mobilereolution = true;
            this.desktopResolution = false;
        }
        else {
            this.desktopResolution = true;
            this.mobilereolution = false;
        }
    }
    ngOnChanges(): void {
         window.scroll(0, 0);
        if (!(this.artistname == this.field_artist_name)) {

            this._newsService.getNodeCount(this.type, this.field_artist_name).subscribe(
                data => {
                    this.totalNewsItems = parseInt(data[0].count);

                    this.setPage(1);
                },
                err => console.error(err),
                () => {

                }
            )
        }
    }

    onResize(event) {
        if (event.target.innerWidth < 767) {
            this.newsMobileCarousel = true;
        } else {
            this.newsMobileCarousel = false;
        }
        if (event.target.innerWidth < 767) {
            this.mobilereolution = true;
            this.desktopResolution = false;
        }
        else {
            this.desktopResolution = true;
            this.mobilereolution = false;
        }
    }
    setPage(pageNumber: number) {

        if (pageNumber < 1 || pageNumber > this.pager.totalPages) {
            return;
        } else {

            this.pager = this.pagerService.getPager(this.totalNewsItems, pageNumber, this.pagedItems);

            this.offset = (pageNumber - 1) * this.pagedItems;
            if (!this.firstInit) {
                if (pageNumber >= 2) {
                    this.slicenumber = 0;
                }
                else {
                    this.slicenumber = 1;
                }
                this.getNews();
            }
            if (this.pager.currentPage === this.pager.totalPages) {
                this.nextDisable = true;
            }
            window.scroll(0, 0);
        }
    }
}



