import { Component, Input, OnInit, OnChanges } from '@angular/core';
import {Tour, TourService} from '../tour.service';
import { TourSearch } from '../tour-search.pipe';
import { TourBlockComponent } from '../tour-block/tour-block.component';
import { GeneralService } from '../../general.service';
import { Subscription }   from 'rxjs/Subscription';
import { Utils } from '../../utils';

declare var google: any;

@Component({
  selector: 'app-tour-embed',
  templateUrl: 'tour-embed.component.html',
  styleUrls: ['tour-embed.component.css'],
})
export class TourEmbedComponent implements OnInit, OnChanges{
  @Input() count;
  @Input() cols;
  @Input() detailPath: string = "/tour";
  @Input() category: string ;
  @Input() headerText: string;
  @Input() customClass: string;
  @Input() enableNoResult:boolean = false;
  isLocationEnable:any;
  subscription: Subscription;

  public tour: Tour[];
  map;
  empty = false;
  showFilter:boolean = false;
  nearDates: boolean = false;  
  isUpcoming:boolean = true;
  private tempisUpcoming:boolean = true;

   constructor(private _tourService: TourService, private _navigationSource: GeneralService){
      this.isLocationEnable = Utils.loacationObj;
      this.subscription = _navigationSource.navigationSource$.subscribe(
        data => {
          console.log("----------------------------geoLocationInfo--------------------------------------");
          console.log(data);
          this.isLocationEnable = data;
          Utils.loacationObj = data;
        });
  }
    
  clearInput(){
    let element = (<HTMLInputElement>document.getElementById("close"));
    element.value = "";
  }

  filterDates(showFilter:boolean){
    this.nearDates = !this.nearDates;
    //this.nearDates = "";
  }

  switchUpcomingTourdates(){
    this.isUpcoming = true;
  }

  switchPastTourdates(){
    this.isUpcoming = false;
    this.nearDates = false;
  }

  drawMap(){
     var mapProp = {
            center: new google.maps.LatLng(44.7658002, -85.624088),
            zoom: 5,
            mapTypeId: google.maps.MapTypeId.ROADMAP
      };
      var map = new google.maps.Map(document.getElementById("gmap"), mapProp);
  }

  drawMarkers(tour: Tour[]){
    for(let i=0; i<tour.length; i++){
      let marker = new google.maps.Marker({
        position: new google.maps.LatLng(parseFloat(tour[i]["latitude"]), parseFloat(tour[i]["longitude"])),
        map: this.map
      });
    }
  }

  ngOnChanges() {
     
  }

  ngOnInit() {
    //this.drawMap();    
  }
  ngOnDestroy() {
    //this.subscription.unsubscribe();
  }
}


