import { Component, OnInit } from '@angular/core';

import { LivefyreBlockComponent } from '../livefyre/livefyre-block/livefyre-block.component';

import { environment } from '../../environments/environment';
import { Artist, ArtistService } from "../artists/artists.services";
import { News, NewsService } from '../+news/news.service';
import { Router }   from '@angular/router';
import { Utils } from '../utils';
@Component({
    selector: 'app-about',
    templateUrl: './about.component.html',
    styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
    env = environment;
    
    private mobilereolution: boolean = false;
    private desktopResolution: boolean = false;
    public artists: Artist[];
    public artistlength: number;
    public news: News[];
    public selectedNews: News;
    constructor(private _artistService: ArtistService, private _newsService: NewsService, private _router: Router) { }
     getNews(){
        this.selectedNews = undefined;
       
        this.news = [];
        
        this._newsService.getNews("",true).subscribe(
            data => { 
                this.news = data;
            },
            err => console.error( err),
            () => console.log('done')
        );
        
        return this.news;
    }
    
    gotoDetail(news: News) {
        let id:string;
        this.selectedNews = news;
        id = Utils.getSlug(this.selectedNews.path);
        let link = ['/news',id];
        this._router.navigate(link);
    }

    getArtists(): Artist[] {
        this.artists = [];

        this._artistService.getArtists().subscribe(
            data => {
                this.artists = data;
                console.log('this.artists');
                console.log(this.artists);
            },
            err => console.error(err),
            () => {
                this.artistlength = this.artists.length;


            }
        );
        return this.artists;
    }
  ngOnInit() {
        window.scroll(0, 0);
      this.getArtists();
      if(window.screen.width < 767){
          this.mobilereolution = true;
          this.desktopResolution = false;
      }
      if(window.screen.width > 767){
          this.desktopResolution = true;
          this.mobilereolution = false;
      }
  }
   onResize(event) {
      if(event.target.innerWidth < 767){
          this.mobilereolution = true;
          this.desktopResolution = false;
      }
      if(event.target.innerWidth > 767){
          this.desktopResolution = true;
          this.mobilereolution = false;
      } 
  }

}



