import { Component, Input, OnInit, OnChanges } from '@angular/core';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-social-buttons',
  templateUrl: 'social-buttons.component.html',
  styleUrls: ['social-buttons.component.css']
})

export class SocialButtonsComponent implements OnInit, OnChanges {
  @Input() link: string;
  @Input() facebook: boolean = true;
  @Input() googlePlus: boolean = true;
  @Input() twitter: boolean = true;
  @Input() mailto: boolean = true;
  @Input() pagetitle:string;
  public hostURL: string;
  public windowDim: string = "width=600,height=450";
  @Input() customClass: string;
  location: string = "";

  constructor() { }

  facebookClick() {
    if (this.link) {
      window.open("http://www.facebook.com/sharer.php?u=" + this.location, "_blank", this.windowDim);
    
    }
  }

  googlePlusClick() {
    if (this.link) {
      window.open("https://plus.google.com/share?url=" + this.location, "_blank", this.windowDim);
    }
  }

  twitterClick() {
    if (this.link) {

      window.open("https://twitter.com/intent/tweet?url=" + this.location +"&text="+this.pagetitle+"&via=WMNashville", "_blank", this.windowDim);
    }
  }

  mailtoClick() {
    if (this.link) {
      window.open("mailto:?subject=" + this.location, "_blank", this.windowDim);
    }
  }

  ngOnInit() {
   
   // this.hostURL = "http://" + environment.host + "/";
    //this.location = window.location.href;
    //console.log("window.location.href "+window.location.href);
     this.hostURL = "http://" + environment.host + "/";
    if(this.link){
      this.location = this.link;  
    }else{
      this.location = window.location.href;
    }    
  }

  ngOnChanges() {
    this.location = window.location.href;
  }

}


