import { Component, Input, OnInit, OnChanges, ElementRef } from '@angular/core';
import { environment } from '../../../environments/environment';
import {WindowReference} from '../window-reference';

@Component({
  selector: 'app-twitter-embed',
  templateUrl: 'twitter-embed.component.html',
  styleUrls: ['twitter-embed.component.css']
})

export class TwitterEmbedComponent implements OnInit, OnChanges {

  tweetId:string;
  node:ElementRef = null;

  @Input('tweetId') set setTweetId(tweetId:string){
    this.tweetId = tweetId;
    let win = WindowReference.get();
    win.twttr.ready(() => {
      win.twttr.widgets.createTweet(this.tweetId, this.node.nativeElement.querySelectorAll(".twitter-embed")[0]);
    });
  }

  constructor(element:ElementRef) {
    this.node = element;
  }

  ngOnInit() {
    //console.log("window.location.href "+window.location.href);
  }

  ngOnChanges() {
  }
}


