import { Component, Input, OnInit} from '@angular/core';
import {Router,ActivatedRoute,Params } from '@angular/router'
import { MusicService, Audio } from '../music.service';
import { SocialButtonsComponent } from "../../shared/social-buttons"
import {SpotifyService} from '../spotify.service';

@Component({
    selector: 'app-track-detail',
    templateUrl: 'track-detail.component.html',
    styleUrls: ['track-detail.component.css'],
    providers: [MusicService, SpotifyService]
})
export class TrackDetailComponent{
  @Input() audio: Audio;
  public id:string;
  constructor(private _musicService: MusicService,
      private _router: ActivatedRoute, private _spotifyService: SpotifyService) {}

  ngOnInit(): void {
    this._router.params.forEach((params: Params) => {
    this.id = params['id'];
    this._musicService.getAudioDetail(this.id).subscribe(
      data => {
        this.audio = data[0];
        this.mapSpotifyData();
      },
      err => console.log(err),
      () => console.log('done')
    );
  });
  window.scroll(0, 0);
  }

  mapSpotifyData() {
    this._spotifyService.getSpotifyData(this.audio.spotifyTrackID)
      .subscribe(
        data => {
          this.audio.albumArt = data.album.images[0].url;
          this.audio.source = data.preview_url;
          this.audio.isPlaying = false;
          this.audio["artist"] = data.artists[0].name;
        },
        err => console.error(err),
        () => console.log('done')
      );
  }

  onAudioPlayerStateChange(event, index) {
    //if(event.isPlaying){
      this.audio.isPlaying = event.isPlaying;
    //}
  }
  
}



