import { Component, Input, OnInit } from '@angular/core';
import {Block, BlockService } from './block.service';
import {DomSanitizer} from '@angular/platform-browser';


@Component({
  selector: 'app-block',
  templateUrl: 'block.component.html',
  styleUrls: ['block.component.css']
})
export class BlockComponent implements OnInit {
  @Input() blockID;
  public block: Block;
  private safeHtml: any;

  constructor(private _blockService: BlockService,private sanitizer: DomSanitizer) { }

  getBlock() {
    this._blockService.getBlock(this.blockID).subscribe(
      data => {
        this.block = data[0];
        this.safeHtml = this.sanitizer.bypassSecurityTrustHtml(this.block.body.toString());
      },
      err => console.error(err),
      () => console.log('done')
    );

  }

  ngOnInit() {
    this.getBlock();
  }

}



