import { trigger, state, style, transition, animate } from '@angular/core';

export const hoverOverlay = trigger('hoverOverlay', [
    state('in', style({ opacity: 1 })),
    state('out', style({ opacity: 0 }))
])

export const hoverEffectFade = trigger('hoverEffectFade', [
    state('in', style({})),
    state('out', style({})),
    transition('in <=> out', animate("1000ms ease-in"))
])

export const fadeIn = trigger('fadeIn', [
    state('hidden', style({opacity: 0})),
    state('in', style({opacity: 1})),
    state('out1,', style({opacity: 0.5})),
    //state('out2,', style({opacity: 0.3,transform:"translateY(15%)"})),
    state('out2,', style({opacity: 0.1})),
    transition('out1 <=> in',[style({}),animate("0.2s ease-in-out")] ),
    transition('out1 <=> out2',[style({}),animate("0.5s ease-in-out")] ),
    transition('out2 <=> out1',[style({}),animate("0.5s ease-in-out")] ),
    transition('hidden <=> out2',[style({}),animate("0.2s ease-in-out")] ),
    transition('hidden => in',[style({}),animate("1s ease-in-out")] )
])

export const tourButton = trigger('tourButton', [
    state('out', style({opacity: 0,"margin-bottom": "0px"})),
    state('in', style({opacity:1,"margin-bottom": "30px"})),
    transition('out => in',[style({}),animate("0.6s ease-in-out")] )
])

export const delayedButtonFade = trigger('delayedButtonFade', [
    state('out', style({opacity: 0})),
    state('in', style({opacity:1})),
    transition('out => in',[style({}),animate("1s ease-in-out")] )
])


