import { Component, EventEmitter, OnInit, OnChanges, ViewChild, Input, Output, ElementRef, AfterViewChecked, AfterContentInit, AfterContentChecked } from '@angular/core';
import { CarouselConfig } from '../../carousel/carousel-config';
import { CarouselComponent } from '../../carousel/carousel.component';
import {Artist, ArtistService} from '../artists.services';
import {Utils} from '../../utils';
import {Router,ActivatedRoute,Params } from '@angular/router';

@Component({
  selector: 'app-artists-carousel',
  templateUrl: './artists-carousel.component.html',
  styleUrls: ['./artists-carousel.component.css'],
  host: {
    	'(window:resize)': 'onResize($event)'
  	}
})
export class ArtistsCarouselComponent extends CarouselComponent implements OnInit {
 @Input() artists: Artist[];
 public carouselimageURL: String="";
 @Input() carouseln:number;
 @Input() spotifyCarousel: boolean = false;
  public selectedArtist: Artist;
  
  constructor(element:ElementRef , private _router: Router) { 
      super();
		  this.node = element;
		  this.carouselLength = 0;
		  this.options = {
			  itemCount: 3, 
			  showDots: false, 
			  showNav: true, 
			  animationSpeed: 0.5, 
			  autoplay: false, 
			  loop: true,
			  responsive:{
			  	"767": 1,
				  "1200": 3,
				  "1400": 6,
			  	"2700": 6
			  }
		  };

    }

  ngOnInit() {

		 
  }
  	ngOnChanges() {
		
		  this.carouselLength = this.carouseln;
		for(let index in this.artists){
			this.artists[index].field_banner_image = Utils.getImageSrc(this.artists[index].field_banner_image.toString());
			this.carouselimageURL=this.artists[index].field_banner_image;
			
		}
        if(this.artists.length == this.carouselLength){
            
			this.duplicate = this.artists.slice();
			
            this.init();
            this.createCarousel = true;
        }
		else{
			
		}
	}
	gotoArtistDetail(artist: Artist) {
		
      let id:string;
      this.selectedArtist = artist;
      id = Utils.getSlug(this.selectedArtist.path)  
      let link = ['/artist',id];
      this._router.navigate(link);
  }
}



