export class Utils {

    public static loacationObj: any;

    constructor() { }

    public static getNID(urlSlug: String) {
        var slugArray = urlSlug.split('-');
        return slugArray.pop();
    }
    public static getTitle(urlSlug: String) {
       var index= urlSlug.search("-and-");
        var slugArrayTEmp=urlSlug;
       if(index >1){
       slugArrayTEmp = urlSlug.split('and').join('%2B');
       }
       
        var slugArray =  slugArrayTEmp.split('-').join('%20');
        return slugArray;
    }
    public static getSlug(path: String) {
        var pathArray = path.split('/');
        return pathArray.pop();
    }
    public static getroutinglink(path: String) {
        let finalURL;

        var a = path.split(',');
        //var a = ['<a href="/artist/blake-shelton" hreflang="en">Blake Shelton</a>', '<a href="/artist/dan-shay" hreflang="en">Dan + Shay</a>'];
        a = a.map(function (x) { return x.replace(/"([^"]+(?="))"/g, '$1') });
        a = a.map(function (x) { return x.replace(/a href=/g, "a [routerLink]=\"\['") });
        a = a.map(function (x) { return x.replace(/\hreflang/g, "']\"\ hreflang") });
        a = a.map(function (x) { return x.replace(/hreflang=en/g, "") });
        finalURL = a;
        return finalURL;
    }

    public static getImageSrc(imgtag: string) {
        let imgsrc = "";
        let srcRegex = /src[\s]*=[\s\\]*"([^"]*)"/g;
        let imgSrcAr = srcRegex.exec(imgtag);
        if (imgSrcAr && imgSrcAr[1]) {
            imgsrc = imgSrcAr[1];
        }
        return decodeURIComponent(imgsrc);
    }
    public static getHref(path: string) {
        let imgsrc = "";
        let srcRegex = /href[\s]*=[\s\\]*"([^"]*)"/g;
        let imgSrcAr = srcRegex.exec(path);
        if (imgSrcAr && imgSrcAr[1]) {
            imgsrc = imgSrcAr[1];
        }
        return decodeURIComponent(imgsrc);
    }
    public static getImagesSrc(imgtag: string) {
        let match;
        let imgAr = [];
        let srcRegex = /src[\s]*=[\s\\]*"([^"]*)"/g;
        while (match = srcRegex.exec(imgtag)) {
            imgAr.push(match[1]);
        }
        return imgAr;
    }
    public static getArrayofObjects(malformedArray: string) {
        return JSON.parse("[" + malformedArray + "]");
    }

    public static addClass(el, className) {
        if (el.classList)
            el.classList.add(className)
        else if (!Utils.hasClass(el, className)) el.className += " " + className
    }

    public static removeClass(el, className) {
        if (el.classList)
            el.classList.remove(className)
        else if (Utils.hasClass(el, className)) {
            var reg = new RegExp('(\\s|^)' + className + '(\\s|$)')
            el.className = el.className.replace(reg, ' ')
        }
    }

    private static viewportWidth(): number {
        let node = document.getElementsByTagName("body");
        //console.log(" node[0].clientWidth "+node[0].clientWidth);
        return node[0].clientWidth;
    }

    public static checkMobile(): boolean {
        return Utils.viewportWidth() < 767;
    }

    public static checkIpad(): boolean {
        return Utils.viewportWidth() > 768 && Utils.viewportWidth() < 1024;
    }

    private static hasClass(el, className) {
        if (el.classList)
            return el.classList.contains(className)
        else
            return !!el.className.match(new RegExp('(\\s|^)' + className + '(\\s|$)'))
    }
}


