import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterByArtist'
})
export class FilterByArtist implements PipeTransform {
  transform(value: any, args?: any): any {
    let filter = args.toLocaleLowerCase();
    if (value != undefined) {
      if (args != "" && args != undefined) {
        let filter = args.toLocaleLowerCase();
        if (filter) {
          value = value.filter(t => t.tags.toLocaleLowerCase().indexOf(filter) != -1);
        }
        if (value.length > 0) {
          return value;
        }
      }


    }
  }
}


