import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'FilterByNewsID'
})
export class FilterByNewsID implements PipeTransform {
    transform(value: any, args?: any): any {
        console.log("this is args");
        console.log(args);
        
        if (value != undefined) {
            if (args != "" && args != undefined) {
                let filter = args.toLocaleLowerCase();
                return filter ? value.filter(t => t.nid.toLocaleLowerCase().indexOf(filter) == -1) : value;
            }
            else {
                return value;
            }
        }
        //return value;
    }
}


