import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { Utils } from '../utils'
import { environment } from '../../environments/environment';

export interface News {
    body: String;
    created: String;
    nid: String;
    path: String;
    title: String;
    thumbnail: String;
    //field_artist_name_1: String;
    //field_artist_name:String;
    field_tags: String;
    shareLink: String;
    showShare: Boolean;
    promote: String;
}


@Injectable()
export class NewsService {

    constructor(private http: Http) { }

    getNews(categoryName: string, featured: boolean): Observable<News[]> {
        let url: string
        if (categoryName == null) {
            url = environment.host + environment.newsListEndpoint;
           
        } else {
            url = environment.host + environment.newsCategoryEndpoint + "/" + categoryName;
        }
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    getArtistNews(): Observable<News[]> {
        let url: string
        url = environment.host + environment.newsArtistEndpoint;
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
          
    }

    getNodeCount(type: string,artistname:string) {
        if(artistname == ""){
        var url: string = environment.host + environment.nodeCount + "?type=" + type;
       
    }
    else{
        var url: string = environment.host + environment.nodeArtisttagCount + "?type=" + type + "&tags=" +artistname;
        
    }
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    getPagernews(offsetValue: number, artistid: string, featured: boolean): Observable<News[]> {
        let url: string

        url = environment.host + environment.newsListEndpoint + "?tags=" + artistid + "&offset=" + offsetValue;

       
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    getNewsIdList(categoryName: string): Observable<News[]> {
        let url: string
        url = environment.host + environment.newsNidCountEndpoint;
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    loadmoreNews(offsetValue: number, categoryName: string): Observable<News[]> {
        let url: string
        if (categoryName == null) {
            url = environment.host + environment.newsListEndpoint + "?offset=" + offsetValue;
        } else {
            url = environment.host + environment.newsCategoryEndpoint + "/" + categoryName + "?offset=" + offsetValue;
        }
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    getNewsDetail(id: String) {
        var nid: String = Utils.getNID(id);
        var url: string = environment.host + environment.newsDetailEndpoint;
        return this.http.get(url + '?nid=' + nid)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    getMinifiedNewsDetail(id: String) {
        var nid: String = Utils.getNID(id);
        var url: string = environment.host + environment.newsDetailMinEndpoint;
        return this.http.get(url + '?nid=' + nid)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    private handleError(error: any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg = error.message || 'Server error';
        console.error(errMsg); // log to console instead
        return Observable.throw(errMsg);
    }



}



