import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { CommentsService, Comments } from '../comments/comments.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Utils } from '../utils';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-comments-sidedrawer',
  templateUrl: './comments-sidedrawer.component.html',
  styleUrls: ['./comments-sidedrawer.component.css']
})
export class CommentsSideDrawerComponent implements OnInit {
  @Input() id: string;
  @Input() showSD: boolean = false;
  @Output() onCloseSideDrawer = new EventEmitter();
  comments: Comments;

  constructor(private _commentsService: CommentsService) { 
        //this.comments = 
  }

  ngOnInit() {
    this.checkCommentCount();
  }

  ngOnDestroy(){
    this.removeClassToBody();
  }

  ngOnChanges() {
    if(this.showSD){
      this.addClassToBody();
    }else{
      this.removeClassToBody();
    }
  }

  checkCommentCount(){
    this._commentsService.getCommentCount(this.id).subscribe(
        data => {
            this.comments = data[0];
            console.log(data[0]);
        },
        err => console.error(err),
        () => console.log('done')
    );
  }

  toggleSideDrawer(){
    this.showSD = !this.showSD;
    this.removeClassToBody();
    this.onCloseSideDrawer.emit(true);
  }

  addClassToBody(){
    let body = document.getElementsByTagName("body")[0];
    console.log(body);
    Utils.addClass(body, "show-SideDrawer");
  }

  removeClassToBody(){
    let body = document.getElementsByTagName("body")[0];
    Utils.removeClass(body, "show-SideDrawer");
  }
}



