import { Pipe } from '@angular/core'

@Pipe({
    name: 'TruncatePipe'
})
export class TruncatePipe {
    transform(value: string, args: number): string {
        let limit = args > 0 ? args : 10;
        let trail =  '...';
        return value.length > limit ? value.substring(0, limit) + trail : value;
    }
}


