import { Injectable, Input } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { Utils } from '../utils'
import { environment } from '../../environments/environment';

export interface Comments {
    nid: String;
    title: String;
    name: String;
    changed: String;
    thread: String;
    user_picture: String;
    comment_count: String;
}

@Injectable()
export class CommentsService {
    nid: string;
    constructor(private http: Http) { }

    getCommentCount(id): Observable<Comments[]> {
        let url: string;
        this.nid = Utils.getNID(id);
        url = environment.host + 'rest/comment/count?nid=' + this.nid;
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    getComments(id): Observable<Comments[]> {
        let url: string;
        this.nid = Utils.getNID(id);
        url = environment.host + 'rest/comment/news?nid=' + this.nid;
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    getLoggedUser(): Observable<any> {
        let url: string;
        url = environment.host + 'rest/user/logged';
        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    generateToken(): Observable<any> {
        return this.http.get(environment.host + 'rest/session/token')
            .map((res: Response) => res.text())
            .catch(this.handleError);
    }

    createComment(commentObj, token): Observable<any> {
        let header = new Headers();
        header.append("Content-Type", "application/hal+json");
        header.append("X-CSRF-Token", token);

        commentObj._links.type.href = environment.host + "rest/type/comment/comment";
        commentObj._links.type[environment.host + "relation/comment/comment/entity_id"] = {};
        commentObj._links.type[environment.host + "relation/comment/comment/entity_id"].href = environment.host + "rest/type/comment/comment/node/" + commentObj.entity_id[0].target_id;

        return this.http.post(
            environment.host + 'entity/comment?_format=hal_json',
            JSON.stringify(commentObj), { headers: header })
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    updateComment(cid, token): Observable<any> {
        let header = new Headers();
        header.append("Content-Type", "application/hal+json");
        header.append("X-CSRF-Token", token);
        return this.http.patch(
            environment.host + '/comment/' + cid + '?_format=hal_json',
            JSON.stringify({
                "_links": {
                    "type": {
                        "href": environment.host + "rest/type/comment/comment",
                        "http://d8.dd:8083/relation/comment/comment/entity_id": {
                            "href": "http://d8.dd:8083/rest/type/comment/comment/node/1"
                        }
                    }
                },
                "subject": [{ "value": "comment angular" }],
                "comment_body": [{ "value": "Body text for the comment." }]
            }),
            { headers: header })
    }

    deleteComment(cid, token): Observable<any> {
        let header = new Headers();
        header.append("Content-Type", "application/hal+json");
        header.append("X-CSRF-Token", token);

        return this.http.delete(
            environment.host + 'comment/' + cid + '?_format=hal_json',
            { headers: header })
            .map((res: Response) => {
                if (res.status === 204) {
                    return {};
                }
            })
            .catch(this.handleError);
    }
    
    private handleError(error: any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg = error.message || 'Server error';
        console.error(errMsg); // log to console instead
        return Observable.throw(errMsg);
    }
}



