import { Component, OnInit, HostListener, AfterViewInit} from '@angular/core';
import { fadeIn,tourButton,delayedButtonFade } from '../animations/animations';
import { Inject} from "@angular/core";
import { DOCUMENT } from '@angular/platform-browser';
import {PageScrollConfig} from 'ng2-page-scroll';

@Component({
  selector: 'app-home',
  templateUrl: 'home.component.html',
  styleUrls: ['home.component.css'],
  animations: [fadeIn, tourButton,delayedButtonFade],
  host: {
    '(window:resize)': 'onResize($event)',
    '(window:onload)': 'onLoad($event)'
  }
})

export class HomeComponent implements OnInit, AfterViewInit  {
  artistTourFadeIn: string = "hidden";
  tourButtonAnimate: string = "out";
  smartHeader: string = "tour";
  delayedButtonFade :string = "out";
  smartHeaderConfig = { "tourPanel": "tour", "herovideoBlock": "tour", "musicPanel": "music", "videoPanel": "video", "photoPanel": "photo", "newsPanel": "news" };
  videoStyle:any = {
    height: "auto",
    width: "1366px",
    top: "0px",
    left: "0px",
  };

  constructor(@Inject(DOCUMENT) private document: Document) {
    setTimeout(() => {
      this.artistTourFadeIn = "in";
    }, 2000);
    /*
    setTimeout(() => {
      this.artistTourFadeIn = "in";
    },1000);
    */
    setTimeout(() => {
      this.tourButtonAnimate = "in";
    }, 2500);
     PageScrollConfig.defaultEasingLogic = {
            ease: (t: number, b: number, c: number, d: number): number => {
                // easeInOutExpo easing
                if (t === 0) return b;
                if (t === d) return b + c;
                if ((t /= d / 2) < 1) return c / 2 * Math.pow(2, 10 * (t - 1)) + b;
                return c / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
            }
        };
  }

  @HostListener('window:scroll', ['$event'])
  updateOnScroll(event) {
    /*Smart Header script*/
    //scroll { target: HTMLDocument → /, isTrusted: true, view: Window → /, detail: 0, layerX: 0, layerY: 0, pageX: 0, pageY: 3420, which: 0, rangeOffset: 0, cancelBubble: false }
    let section = <HTMLScriptElement[]><any>document.getElementsByClassName('homePanelCon');
    let nav_height = document.getElementById("fixed-nav").offsetHeight;
    for (let i = 0; i < section.length; i++) {
      var sectionTop = section[i].offsetTop - nav_height;
      let sectionBottom = sectionTop + section[i].offsetHeight;
      let scrollTop = event.pageY | document.body.scrollTop;
      if (scrollTop >= sectionTop && scrollTop <= sectionBottom) {
        this.smartHeader = this.smartHeaderConfig[section[i].classList[1]];  
        this.delayedButtonFade = "out";      
        this.delayedButtonFade = "in";
      }
    }

    let scrollTop = event.pageY | document.body.scrollTop;
    //console.log(scrollTop);
    /*Fade in Animations on scroll*/
    if (scrollTop >= 300) {
      this.artistTourFadeIn = "out2";
    } else if (scrollTop >= 150) {
      this.artistTourFadeIn = "out1";
    }
    else {
      this.artistTourFadeIn = "in";
    }
  }
  ngAfterViewInit(){
    var element:any = document.getElementById('myVideo');
    element.muted = "muted";
    this.resizeVideo();
  }

  ngOnInit() {    
    window.scroll(0, 0);
    let body = document.getElementsByTagName('body')[0].classList.add("home");
  }
  ngOnDestroy() {
    let body = document.getElementsByTagName('body')[0].classList.remove("home");
  }

  getTicketsClick() {
  	window.scroll(0, document.getElementById("tour").offsetTop); 
  }

  resizeVideo(){
    var wwidth = window.innerWidth;
    var wheight = window.innerHeight;   

     let ratio =  ( 720 / 1280 ) * 100;

     let calculatedSystemWidth = ( (1280 / 720) * wheight );

     if(calculatedSystemWidth < wwidth){
       calculatedSystemWidth = wwidth;
     }

      this.videoStyle.width = calculatedSystemWidth + "px";      
   }  

  onLoad(event){
    this.resizeVideo();
  }
  onResize(event){
    this.resizeVideo();
   }
   
  /*scrollToArtist(){
     this.document.body.scrollTop = 900;
  }   */
}


