import { Component, Input, OnInit } from '@angular/core';
import { Utils } from '../../utils'
import {FeaturedArtist, FeaturedArtistService} from '../featuredartist.service';
import { Router }   from '@angular/router';

@Component({
  selector: 'app-featuredartist',
  templateUrl: './featuredartist.component.html',
  styleUrls: ['./featuredartist.component.css'],
  providers: [FeaturedArtistService]
})
export class FeaturedartistComponent implements OnInit {

  public featuredArtist: FeaturedArtist[];
  @Input() count;
  @Input() cols;
  @Input() customClass: string;
   private mobilereolution: boolean = false;
    private desktopResolution: boolean = false;


  constructor(private _artistService: FeaturedArtistService) { }
  getFeaturedArtist(){
      this.featuredArtist = [];
      
      this._artistService.getFeaturedArtist().subscribe(
          data => {
              this.featuredArtist = data;
          },
          err => console.error( err),
          () => console.log('done')
      );
      
      return this.featuredArtist;               
  }


  ngOnInit() {
    this.featuredArtist = this.getFeaturedArtist();  
     
      if(window.screen.width < 767){
          this.mobilereolution = true;
          this.desktopResolution = false;
      }
      if(window.screen.width > 767){
          this.desktopResolution = true;
          this.mobilereolution = false;
      }   
  }
  
    onResizefot(event) {
      if(event.target.innerWidth < 767){
          this.mobilereolution = true;
          this.desktopResolution = false;
      }
      if(event.target.innerWidth > 767){
          this.desktopResolution = true;
          this.mobilereolution = false;
      } 
  }

}



