import { Injectable } from '@angular/core';
import { Http, Response, Jsonp } from '@angular/http';
import {Observable} from 'rxjs/Rx';
import { environment } from '../../environments/environment';

export interface Tour {
  title: String;
  nid: String;
  path: String;
  event_date: Date;
  venue_city: string;
  venue_state: string;
  venue_country: string;
  venue_place: string;
  acts: string;
  ticket_url: string;
  rsvp_url: string;
  vip_url: string;
  ticket_status: string; 
}

@Injectable()
export class TourService {

  constructor(private jsonp: Jsonp, private http: Http) { }

  getTour() {
    var url: string =  environment.tourEndpoint + "&callback=JSONP_CALLBACK";
    return this.jsonp.get(url )
      .map((res: Response) => res.json())
      .catch(this.handleError);
  }

  geolocate(timezone:Boolean, cityPrecision:Boolean, objectVar:String) {
      let api: string = "ip-country";
      let domain: string = 'api.ipinfodb.com';
      let apiKey = ['210163b03ba3bf329a8ede992a24d6364ceff96a397c565f6acb93b753cc30db','00c0b9e4900011af427a05e45bf07855fd7d801b51947dcef1cec8a5165b8f5d','b8169a15f1dfac4e95c07ba3c2c002f3858cef89329305392e21e495ea61063f','a4d44d0de8e02cb44ae857a70ba9ec04388f810862a08ad35dbffe4317981357'];
      let randomNumber:number = Math.round(Math.random());
      let url: string = "http://" + domain + "/v3/" + api + "/?key="+apiKey[randomNumber]+"&format=json";
      console.log(url);
      console.log("----------------------------api.ipinfodb.com----------------------------------- "+randomNumber);
      return this.http.get(url)
        .map((res: Response) => res.json())
        .catch(this.handleError);
  }

  mapTourInfo(data): Tour[] {
    let tourDates: Array<Tour> = new Array<Tour>();
    
    for (let t of data){
      let tourDate = <Tour>{};
      tourDate.title = t.title;
      tourDate.nid = t.id;
      tourDate.venue_city = t.venue.city;
      tourDate.event_date = new Date(t.datetime);
      tourDate.venue_state = t.venue.region;
      tourDate.venue_country = t.venue.country;
      tourDate.venue_place = t.venue.name;
      tourDate.ticket_url = t.ticket_url;
      tourDate.rsvp_url = t.facebook_rsvp_url;
      tourDate.vip_url = t.custom_url;
      tourDate.ticket_status = t.ticket_status;
      tourDates.push(tourDate);
    }

    return tourDates;
  }

  getTourContent(past:Boolean = false) {
    var url: string =  environment.host + environment.tourEndpoint;
    if(past) url =  environment.host + environment.pastTourEndpoint;

      return this.http.get(url)
          .map((res: Response) => res.json())
          .catch(this.handleError);  
    }

  private handleError(error: any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg = error.message || 'Server error';
    console.error(errMsg); // log to console instead
    return Observable.throw(errMsg);
  }

}



