import { Injectable } from '@angular/core';
import { Http, Response, Jsonp } from '@angular/http';
import {Observable} from 'rxjs/Rx';
import { environment } from '../../../environments/environment';

export interface Livefyre {
    title: String;
    thumbnail_url: String;
    thumbnail_url_large: String;
    content_url: String;
    type: String;
    video_url: String;
    video_coverImage: String;

}


var Type;

@Injectable()
export class LivefyreService {

     constructor(private jsonp: Jsonp, private http: Http) { }

    getLivefyre() {
        var url: string =  environment.livefyreEndpoint + "&callback=JSONP_CALLBACK";
        //console.log(url);
        return this.jsonp.get(url )
        .map((res: Response) => res.json())
        .catch(this.handleError);
    }
    maplivefyreInfo(data): Livefyre[] { //mapping JSON values to Interface
        let LivefyreContents: Array<Livefyre> = new Array<Livefyre>();  
         for (let t of data.headDocument.content){ //t points to data.headDocument.content array
            let LivefyreContent = <Livefyre>{};
             //LivefyreContent.title = t.content.authorId;

             if(t.content.attachments == undefined){ // contents without thumbnail Image
                continue;
             }
             else{  //eliminating contents without thumbnail Image
                for (let a of t.content.attachments){ //a points to data.headDocument.content.content.attachments array
                    LivefyreContent.title = a.title;
                    LivefyreContent.thumbnail_url = a.thumbnail_url;
                    LivefyreContent.content_url = a.link;
                    LivefyreContent.thumbnail_url_large = a.url;
                    LivefyreContent.type = a.type;
                    if(a.type == "video"){
                        //  LivefyreContent.video_coverImage = a.thumbnail_url;
                        var videoURL = a.html,
                        regexp = new RegExp('src="([^"]+)"');
                        var VideoURL = videoURL.match(regexp);
                        LivefyreContent.video_url = VideoURL[1];

                    }

                } 
             }
             LivefyreContents.push(LivefyreContent);
         }


        return LivefyreContents;
    }




    private handleError(error: any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg = error.message || 'Server error';
        console.error(errMsg); // log to console instead
        return Observable.throw(errMsg);
    }

}


