import { Component, Input, ElementRef, OnInit, OnChanges } from '@angular/core';

@Component({
  selector: 'app-artist-tour',
  templateUrl: './artist-tour.component.html',
  styleUrls: ['./artist-tour.component.css']
})
export class ArtistTourComponent implements OnInit {
  @Input() customClass: string;
  @Input() artistname: string;
   private firstTime: boolean=true;
  constructor() { }

  ngOnInit() {

    if (this.firstTime) {

      var s = document.createElement('script');
      s.src = "https://widget.bandsintown.com/main.min.js";
      s.className="bandsscript";
      if((document.getElementsByClassName("bandsscript")[0])){
      document.getElementsByClassName("bandsscript")[0].remove();
      }
    document.getElementsByTagName("head")[0].appendChild(s);
 
 
     

    }


  }
  ngOnChanges() {
 

  }

}



