import { Component, EventEmitter, OnInit, OnChanges, ViewChild, Input, Output, ElementRef, AfterViewChecked, AfterContentInit, AfterContentChecked } from '@angular/core';
import { CarouselConfig } from '../../carousel/carousel-config';
import { CarouselComponent } from '../../carousel/carousel.component';
import { Utils } from '../../utils';
import { IntervalObservable } from 'rxjs/observable/IntervalObservable';
import { Observable } from 'rxjs/Rx';
import { Spotify, SpotifyService } from '../Spotify.service';

@Component({
	selector: 'app-spotify-carousel',
	templateUrl: './spotify-carousel.component.html',
	styleUrls: ['./spotify-carousel.component.css'],
	host: {
    	'(window:resize)': 'onResize($event)'
  	}
})
export class SpotifyCarouselComponent extends CarouselComponent implements OnInit {

	@Input() spotify:Spotify[];
	public carouselimageURL: String = "";
	@Input() carouseln: number;

	constructor(element: ElementRef) {
		super();
		this.carouselWidth= "";
		this.node = element;
		this.carouselLength = 3;
		this.newspagecarousel = false;
		this.isspotify=true;
		this.options = {
			itemCount: 1,
			showDots: false,
			showNav: true,
			animationSpeed: 0.5,
			autoplay: false,
			loop: true,
			responsive: {
				"767": 1,
				"1200": 1,
				"1400": 1,
				"2700": 1
			}
		};

	}

	ngOnInit() {
		

	}
	ngOnChanges() {
		
		this.carouselLength = this.carouseln;
		
		if (this.spotify.length == this.carouselLength) {
			
			this.duplicate = this.spotify.slice(0, 3);
			this.carouselLength=this.duplicate.length;
			this.init();
			this.createCarousel = true;
			
		}
		
	}
	onAudioPlayerStateChange(event, index) {
    //if(event.isPlaying){
      this.stopAllPlayer(event, index);
      this.duplicate[index].isPlaying = event.isPlaying;
    //}
    }
    stopAllPlayer(event, index) {
        for(let i = 0; i < this.duplicate.length; i++){
            this.duplicate[i].isPlaying = !event.isPlaying;
        }
    }
	
onResize(event) {
		//this.clientWidth = event.target.innerWidth;
		this.resizeNodes();

	}

}




