import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'FilterMultipleImages'
})
export class FilterMultipleImages implements PipeTransform {
  transform(value: any): any {
      let pathArray = value.split(',');
        return pathArray.pop();    
     
  
    }
}


