import { Component, EventEmitter, OnInit, OnChanges, ViewChild, Input, Output, ElementRef, AfterViewChecked, AfterContentInit, AfterContentChecked } from '@angular/core';
import { CarouselConfig } from '../../carousel/carousel-config';
import { CarouselComponent } from '../../carousel/carousel.component';
import {News} from '../news.service';

@Component({
    selector: 'news-carousel',
	templateUrl: 'news-carousel.component.html',
  	styleUrls: ['news-carousel.component.css'],
	host: {
    	'(window:resize)': 'onResize($event)'
  	}
})
export class NewsCarouselComponent extends CarouselComponent implements OnInit {

    @Input() blogs: News[];

    constructor(element:ElementRef) { 
		super();
		this.node = element;
		this.newspagecarousel=true;
		this.carouselLength = 4;
		this.options = { 
			itemCount: 1, 
			showDots: false, 
			showNav: false, 
			animationSpeed: 0.5, 
			autoplay: false, 
			loop: true
		};
	}
    
	ngOnInit(){
       
	}
	
	ngOnChanges() {
		
        if(this.blogs.length == this.carouselLength){
			this.duplicate = this.blogs.slice(0, 4);
            this.init();
			this.newspagecarousel=true;
            this.createCarousel = true;
        }
	}
}


