import { Component, Input } from '@angular/core';

import {Page, PageService } from './page.service';
import {Router,ActivatedRoute,Params } from '@angular/router'
import {DomSanitizer} from '@angular/platform-browser';


@Component({
  selector: 'app-page',
  templateUrl: 'page.component.html',
  styleUrls: ['page.component.css']
})
export class PageComponent {
  @Input() urlParam;
  pageArray: Page[];
  page: Page;
  private safeHtml: any;

  constructor(private _pageService: PageService,private route: ActivatedRoute,private sanitizer: DomSanitizer) { }

  getPage(urlParam) {
    this._pageService.getPage().subscribe(
      data => {
        this.pageArray = data;
        console.log(data);
        this.page = this.pageArray.find(x => x.path === "/"+urlParam);
        this.safeHtml = this.sanitizer.bypassSecurityTrustHtml(this.page.body.toString());
      },
      err => console.error(err),
      () => console.log('done')
    );
  }

  ngOnInit(): void {
    this.route.params.forEach((params: Params) => {
      this.getPage(params['id']);
    });
  }

}



