import { Component, OnInit, Input } from '@angular/core';
import { NgsRevealModule } from 'ng-scrollreveal';

import { Livefyre, LivefyreService } from './livefyre-block.service';
import { trigger, state, style, animate, transition } from '@angular/core';



@Component({
    selector: 'app-livefyre-block',
    templateUrl: './livefyre-block.component.html',
    styleUrls: ['./livefyre-block.component.css'],
    providers: [LivefyreService],
    animations: [
        trigger('overlay', [
            state('*', style({ transform: 'scale(1)', opacity: 1 })),
            state('void', style({ transform: 'scale(0)', opacity: 0 })),
            transition('void => *', [style({ transform: 'scale(0)', opacity: 0 }), animate('0.5s ease')]),
            transition('* => void', [style({ transform: 'scale(1)', opacity: 1 }), animate('0.5s 0.5s ease')])
        ])
    ]
})



export class LivefyreBlockComponent implements OnInit {
    public selectedPhoto: Livefyre;
    public selectedIndex: number;
    @Input() setLimit: number;

    // public liveFyrefeed: LivefyreBlockComponent[];

    public liveFyrefeed: Livefyre[];

    constructor(private _livefyreService: LivefyreService) { }

    ngOnInit() {
        window.scroll(0, 0);
        this.selectedPhoto = undefined;
        this._livefyreService.getLivefyre().subscribe(
            data => {
                this.liveFyrefeed = data;
                //console.log(this.liveFyrefeed);
                this.liveFyrefeed = this._livefyreService.maplivefyreInfo(this.liveFyrefeed);

            },
            err => console.error(err),
            () => console.log('done')
        );
        return this.liveFyrefeed;

    }
    setSelectedImage(photo: Livefyre, i: number) {
        this.selectedPhoto = photo;
        this.selectedIndex = i;
    }
    onSelect(photo: Livefyre) {
        this.selectedPhoto = photo;
    }
    lightboxClose(closed: boolean) {
        if (closed) {
            this.selectedPhoto = null;
        }
    }

}



