import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { Utils } from '../utils'
import { environment } from '../../environments/environment';

export interface Spotify {
    title: String;
    thumbnail: String;
    album: String;
    Spotify_url: String;
    album_name:String;
    isPlaying: boolean;
}

@Injectable()
export class SpotifyService {

    constructor(private http: Http) { }

    getSpotify(): Observable<Spotify[]> {
        let url: string = environment.SpotifyEndpoint;

        return this.http.get(url)
            .map((res: Response) => res.json())
            .catch(this.handleError);
    }

    mapSpotifyInfo(data): Spotify[] { //mapping JSON values to Interface
        let SpotifyContents: Array<Spotify> = new Array<Spotify>();  
         for (let t of data.header){
            let SpotifyContent = <Spotify>{};

                    SpotifyContent.title = t.title;
                    SpotifyContent.thumbnail = t.thumbnail;
                    SpotifyContent.album = t.album;
                    SpotifyContent.Spotify_url = t.Spotify_url;
                    SpotifyContent.album_name = t.album_name;
                    t.isPlaying = false;
                    SpotifyContent.isPlaying = t.isPlaying;

             SpotifyContents.push(SpotifyContent);
         }


        return SpotifyContents;
    }

    private handleError(error: Response) {
        // in a real world app, we may send the server to some remote logging infrastructure
        // instead of just logging it to the console
        console.error(error);
        return Observable.throw(error.json().error || 'Server error');
    }
}


