import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, NavigationEnd, Params, PRIMARY_OUTLET } from "@angular/router";
import "rxjs/add/operator/filter";

interface IBreadcrumb {
  label: string;
  params?: Params;
  url: string;
}

@Component({
  selector: "breadcrumb",
  template: `
  <div class="breadcrumb-wrapper {{rootClassName}}" [class.root] = "breadcrumbs.length < 1">  
    <div class="g-layout breadcrumb-layout {{rootClassName}}">    
      <ul class="breadcrumb {{rootClassName}}">
        <li><a [routerLink]="['/']">Home</a></li>
        <li *ngFor="let breadcrumb of breadcrumbs" [class.hide] = "breadcrumb.label.length < 1">
          <span>&#8250;</span><a [routerLink]="[breadcrumb.url]">{{breadcrumb.label}}</a>
        </li>
      </ul>
      </div>
    </div>
  `,
  styleUrls: ['./breadcrumb.component.css']
})
export class BreadcrumbComponent implements OnInit {

  public breadcrumbs: IBreadcrumb[];
  rootClassName: String = "";

  constructor(private activatedRoute: ActivatedRoute, private router: Router) {
    this.breadcrumbs = [];
  }

  ngOnInit() {
    const ROUTE_DATA_BREADCRUMB: string = "breadcrumb";

    //subscribe to the NavigationEnd event
    this.router.events.filter(event => event instanceof NavigationEnd).subscribe(event => {
      
      //set breadcrumbs
      let root: ActivatedRoute = this.activatedRoute.root;
      this.rootClassName = "";
      console.log("-------------------------ActivatedRoute-------------------------------");
      console.log(root);
      this.breadcrumbs = this.getBreadcrumbs(root);
      //let breadcrumbs= this.remove_duplicates(this.getBreadcrumbs(root));
      
    });
  }
  
  private getBreadcrumbs(route: ActivatedRoute, url: string="", breadcrumbs: IBreadcrumb[]=[]): IBreadcrumb[] {
    const ROUTE_DATA_BREADCRUMB: string = "breadcrumb";

    //get the child routes
    let children: ActivatedRoute[] = route.children;

    //return if there are no more children
    if (children.length === 0) {
      return breadcrumbs;
    }

    //iterate over each children
    for (let child of children) {
      //verify primary route
      if (child.outlet !== PRIMARY_OUTLET) {
        continue;
      }

      //verify the custom data property "breadcrumb" is specified on the route
      if (!child.snapshot.data.hasOwnProperty(ROUTE_DATA_BREADCRUMB)) {
        return this.getBreadcrumbs(child, url, breadcrumbs);
      }

      //get the route's URL segment
      let routArray = child.snapshot.url.map(segment => segment.path);
      let routeURL: string = routArray.join("/");

      let routeName: string = routArray[routArray.length - 1];

      //append route URL to URL
      url += `/${routeURL}`;
		
		//console.info('url', url);

      //add breadcrumb
      let label:string = child.snapshot.data[ROUTE_DATA_BREADCRUMB];
      if(label.indexOf(":path")!=-1) label = routeName;

      let breadcrumb: IBreadcrumb = {
      	label: label,
        params: child.snapshot.params,
        url: url
      };
      let className = breadcrumb.label;
      if(className.length > 1){
        this.rootClassName = this.rootClassName + " " + className.replace(/ /g,"_");
        this.rootClassName = this.rootClassName.trim().toLocaleLowerCase();
      }
      breadcrumbs.push(breadcrumb);
      
      //recursive
      return this.getBreadcrumbs(child, url, breadcrumbs);
    }
    
    //we should never get here, but just in case
    return breadcrumbs;
  }

  remove_duplicates(arr) {
    let obj = {};
    for (let i = 0; i < arr.length; i++) {
        obj[arr[i]] = true;
    }
    arr = [];
    for (let key in obj) {
        arr.push(key);
    }
    return arr;
}

}



