// - Routes instead of RouteConfig
// - RouterModule instead of provideRoutes
import { Routes, RouterModule } from '@angular/router';

import { Router, NavigationStart, NavigationEnd, NavigationError, NavigationCancel, RoutesRecognized } from '@angular/router';

//import { VideoComponent } from './+video/video.component';
//import { VideoListComponent} from './+video/video-list/video-list.component';
//import { VideoDetailComponent} from './+video/video-detail/video-detail.component';
//import { VideoCategoryComponent } from './+video/video-category/video-category.component';

import { NewsComponent } from './+news/news.component';
import { NewsListComponent } from './+news/news-list/news-list.component';
import { NewsDetailComponent } from './+news/news-detail/news-detail.component';

//import {PhotoComponent} from './+photo/photo.component';
//import {PhotoListComponent} from './+photo/photo-list/photo-list.component'; 
//import { PhotoDetailComponent} from './+photo/photo-detail/photo-detail.component';
//import {PhotoCategoryComponent } from './+photo/photo-category/photo-category.component';
import { AboutComponent } from './about/about.component';
import { LivefyreComponent } from './livefyre/livefyre.component';


import { AppComponent } from './app.component';

import { HomeComponent } from './+home/home.component';

//import {TourComponent} from './+tour/tour.component';
//import {TourListComponent} from './+tour/tour-list/tour-list.component';

import { MusicComponent } from './+music/music.component';
import { MusicListComponent } from './+music/music-list/music-list.component';
import { AlbumDetailComponent } from './+music/album-detail/album-detail.component';
import { TrackDetailComponent } from './+music/track-detail/track-detail.component';

import { PageComponent } from './page/page.component';

import { pageNotFoundComponent } from './pagenotfound/pagenotfound.component';

import { ArtistsComponent } from './artists/artists.component';
import { ArtistLandingComponent } from './artists/artist-landing/artist-landing.component';
import { ArtistDetailComponent } from './artists/artist-detail/artist-detail.component';
import { ArtistblockComponent } from './artists/artist-block/artist-block.component';

import { SpotifyComponent } from './spotify/spotify.component';
import{ContactformComponent} from './contactform/contactform.component';

const routes: Routes = [
    {
        path: '',
        component: HomeComponent,
    },
    {
        path: 'home-page',
        component: HomeComponent,
    },
    {
        path: 'artist',
        component: ArtistsComponent,
        data: {
            breadcrumb: "Artist"
        },
        children: [
            {
                path: '',
                component: ArtistLandingComponent,
                data: {
                    breadcrumb: "Artist Landing"
                }
            },
            {
                path: ':id',
                component: ArtistDetailComponent,
                data: {
                    breadcrumb: "Artist detail"
                }
            }
        ]
    },
    {
        path: 'news',
        component: NewsComponent,
        data: {
            breadcrumb: "News"
        },
        children: [
            {
                path: '',
                component: NewsListComponent,
                data: {
                    breadcrumb: ""
                }
            },
            {
                path: ':id',
                component: NewsDetailComponent,
                data: {
                    breadcrumb: "News detail"
                }
            }
        ]
    },
    {
        path: 'about',
        component: AboutComponent,
        data: {
            breadcrumb: "About"
        }
    },
    {
        path: 'spotify',
        component: SpotifyComponent,
        data: {
            breadcrumb: "Spotify"
        }
    },
    {
        path: 'livefyre',
        component: LivefyreComponent,
        data: {
            breadcrumb: "Livefyre"
        }
    },
    {
        path: 'spotify',
        component: SpotifyComponent,
        data: {
            breadcrumb: "Spotify"
        }
    },
    // {
    //     path:':id',
    //     component : PageComponent,
    //     data: {
    //       breadcrumb: ":label"
    //     }
    // },
    {
        path: 'studio-contact-form',
        component: ContactformComponent,
        data: {
            breadcrumb: "studio-contact-form"
        }
    },
    {
        path: '404',
        component: pageNotFoundComponent,
        data: {
            breadcrumb: ":label"
        }
    },
    {
        path: '**',
        redirectTo: '/404',
        data: {
            breadcrumb: ":label"
        }
    }
];

// - Updated Export
export const routing = RouterModule.forRoot(routes);



