import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-custom-audio',
  templateUrl: 'custom-audio.component.html',
  styleUrls: ['custom-audio.component.css']
})
export class CustomAudioComponent implements OnInit {
  @Input() src: string;
  @Input() albumart: string;
  @Input() forcestop: boolean = false;
  @Output() playerStateChange:EventEmitter<any> =  new EventEmitter();
  @Input() customClass: string = "";
  @Input() artist: string;
  @Input() title: string;
  public audio:any;
  public currentTime: number = 0;
  public isPlaying: boolean = false;
  
  constructor() {}

  togglePlay(){
    this.isPlaying = !this.isPlaying;
    this.playerStateChange.emit({ isPlaying: this.isPlaying });
  }

  ngOnChanges() {
    this.isPlaying = !this.forcestop;
    if(!this.isPlaying && this.audio){
      this.audio.pause();
    }
    //console.log("-----------------------this.forcestop-------------------------");
    //console.log(this.forcestop);
    //console.log("--------------------------------------------------------------");
  }

  play(){
    this.audio.play();
    //this.forcestop = false;
    /*
    setTimeout(function () {
      this.audio.play();
    }, 150);*/
    this.togglePlay();
  }

  pause(){
    this.audio.pause();
    //this.forcestop = true;
    //this.togglePlay();
    this.isPlaying = !this.isPlaying;
  }

  seek(event:any){
    this.audio.currentTime = event.target.value;
  }

  ngOnInit() {
    //console.log("Audio - "+this.src);    
    this.audio = new Audio(this.src);
    this.audio.ontimeupdate = ( () => {
      this.currentTime = this.audio.currentTime;
    })
    this.audio.onended = (()=>{
      this.isPlaying = !this.isPlaying;
      //this.togglePlay();
    })
  }
}

