import { Component, Input, OnInit } from '@angular/core';
import { Utils } from '../../utils'
import {Artist, ArtistService} from '../artists.services';
import { Router }   from '@angular/router';

@Component({
  selector: 'app-artist-landing',
  templateUrl: './artist-landing.component.html',
  styleUrls: ['./artist-landing.component.css'],
  providers:  [ArtistService]
})
export class ArtistLandingComponent implements OnInit {

    SWIPE_ACTION = { LEFT: 'swipeleft', RIGHT: 'swiperight', UP: 'swipeup'};

  public artists: Artist[];
  public selectedArtist: Artist;
  @Input() count;
  @Input() cols;
  @Input() customClass: string;
  @Input() feildtype:String="All" ;
  public category:String;
  public listview:Boolean=false;
  @Input() artistHomView: Boolean=false;
   @Input() carouselview:Boolean=true;
   public setActive: number = 1;
   public selectedTab: string = "Artists";

   
    private mobileResolution: boolean = false;
    private desktopResolution: boolean = false;


  constructor(private _artistService: ArtistService, private _router: Router) { }

  
  getArtists(){
      this.selectedArtist = undefined;
      this.artists = [];

      this._artistService.getArtists().subscribe(
          data => {
              this.artists = data;
              console.log('this.artists');
              console.log(this.artists);
          },
          err => console.error( err),
          () => {
              console.log('done')
          }
      );
  }

  gotoArtistDetail(artist: Artist) {
      let id:string;
      this.selectedArtist = artist;
      id = Utils.getSlug(this.selectedArtist.path)  
      let link = ['/artists',id];
      this._router.navigate(link);
  }
  ngOnInit() {
        window.scroll(0, 0);
   this.getArtists();
   if (window.screen.width < 1023) {
        this.mobileResolution = true;
        this.desktopResolution = false;
    }
    else {
        this.desktopResolution = true;
        this.mobileResolution = false;
    }
  }

  TabSelected(Clickedcategory: String){
    this.category = Clickedcategory;
    switch(Clickedcategory){
        case 'Artists' : this.setActive = 1; this.selectedTab = "Artists"; break;
        case 'Comedians' : this.setActive = 2; this.selectedTab = "Comedians"; break;
        case 'Others' : this.setActive = 3; this.selectedTab = "Others"; break;
        case 'All' : this.setActive = 4; this.selectedTab = "All"; break;
    }
   // console.info(this.setActive);
  }
  	swipe(action: string = this.SWIPE_ACTION.RIGHT) {
        
       // if (this.setActive > 4 || this.setActive < 1) return;
        
        // next

		if (action == this.SWIPE_ACTION.UP) {
		}
		
        if (action == this.SWIPE_ACTION.RIGHT) {
            if (this.setActive < 2){
                this.setActive = 4;
            }
            else{
                this.setActive = this.setActive - 1;
            }
			switch(this.setActive){
                case 1 : this.selectedTab = "Artists"; break;
                case 2 : this.selectedTab = "Comedians"; break;
                case 3 : this.selectedTab = "Others"; break;
                case 4 : this.selectedTab = "All"; break;
            }
        }

        // previous
        if (action == this.SWIPE_ACTION.LEFT) {
             if (this.setActive > 3){
                this.setActive = 1;
            }
            else{
                this.setActive = this.setActive + 1;
            }
            switch(this.setActive){
                case 1 : this.selectedTab = "Artists"; break;
                case 2 : this.selectedTab = "Comedians"; break;
                case 3 : this.selectedTab = "Others"; break;
                case 4 : this.selectedTab = "All"; break;
            }
        }
         //console.info(this.setActive);
        // toggle avatar visibility
    }
    onResize(event) {

        if (event.target.innerWidth < 1023) {
            this.mobileResolution = true;
            this.desktopResolution = false;
        }
        else {
            this.desktopResolution = true;
            this.mobileResolution = false;
        }
    }

}



