import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterByLocation'
})
export class FilterByLocation implements PipeTransform {
  transform(value: any, args?: any): any {
    console.log("filterCountry this is args");
    return args ? value.filter(t=> t.distance && t.distance <= Number(160)) : value;
  }
}


