import { Component, EventEmitter, OnInit, Input, Output } from '@angular/core';
import { Photo } from "../+photo/photo.service";
import { Livefyre} from "../livefyre/livefyre-block/livefyre-block.service";
import { Utils } from "../utils";
import { CommentsService, Comments } from '../comments/comments.service';
import {  trigger, state, style, animate, transition } from '@angular/core';

@Component({
    selector: 'app-lightbox',
    templateUrl: 'lightbox.component.html',
    styleUrls: ['lightbox.component.css']    
})
export class LightboxComponent implements OnInit {
    @Input() photo: Photo[];
    @Input() livefyre: Livefyre[];
    @Input() showComments:boolean = false;
    @Output() onClose = new EventEmitter();
    @Input() selectedIndex: number;
    @Input() setLimit: number;
   
    comments: Comments;
    
    constructor(private _commentsService: CommentsService) { 
        //this.comments = 
    }

    closed() {
        this.removeClassToBody();
        this.onClose.emit(true);
    }
    nextPhoto() {
        if (this.photo != undefined){
            if (this.selectedIndex < (this.photo.length - 1)) {
                this.selectedIndex = this.selectedIndex + 1;
            }
            else {
                this.selectedIndex = 0;
            }
            this.showCommentCount();
        } 
        if (this.livefyre != undefined){
            if (this.setLimit != undefined){
                if ((this.selectedIndex < (this.livefyre.length - 1) ) && (this.selectedIndex < (this.setLimit - 1))  ) {
                    this.selectedIndex = this.selectedIndex + 1;
                }
                else {
                    this.selectedIndex = 0;
                }
            }
            else{
                if ((this.selectedIndex < (this.livefyre.length - 1) )  ) {
                    this.selectedIndex = this.selectedIndex + 1;
                }
                else {
                    this.selectedIndex = 0;
                }
            }
        } 
    }
    prevPhoto() {
        if (this.photo != undefined){
            if(this.selectedIndex > 0 && (this.photo.length - 1)){
                this.selectedIndex = this.selectedIndex - 1;
            }
            else{
            this.selectedIndex = this.photo.length-1; 
            }
            this.showCommentCount();
        }
        if (this.livefyre != undefined){
            if(this.setLimit != undefined){
                if(this.selectedIndex > 0 && (this.setLimit - 1)){
                    this.selectedIndex = this.selectedIndex - 1;
                }
                else{
                this.selectedIndex = this.setLimit - 1; 
                }
            }
            else{
                if(this.selectedIndex > 0 && (this.livefyre.length - 1)){
                    this.selectedIndex = this.selectedIndex - 1;
                }
                else{
                this.selectedIndex = this.livefyre.length - 1; 
                }
            }
        }      
    }

    ngOnInit() { 
        this.addClassToBody();
    }

    ngOnChanges() {
        this.showCommentCount();
    }

    showCommentCount(){
        if(this.showComments){
            let id = this.photo[this.selectedIndex].nid;
            this._commentsService.getCommentCount(id).subscribe(
                data => {
                    this.comments = data[0];
                    console.log(data[0]);
                },
                err => console.error(err),
                () => console.log('done')
            );
        }
    }

    addClassToBody(){
        let body = document.getElementsByTagName("body")[0];
        console.log(body);
        Utils.addClass(body, "show-photo-lightbox");
    }

    removeClassToBody(){
        let body = document.getElementsByTagName("body")[0];
        Utils.removeClass(body, "show-photo-lightbox");
    }

    ngOnDestroy(){
        this.removeClassToBody();
    }

    
}


