import { Component, EventEmitter, OnInit, Input, Output } from '@angular/core';
import { MusicService, Album, Audio,Embed } from '../music.service';
import { SocialButtonsComponent } from "../../shared/social-buttons";
import { Utils } from "../../utils";
import {NgSwitch, NgSwitchDefault} from '@angular/common/index';
import {Router,ActivatedRoute,Params } from '@angular/router'
import {DomSanitizer} from '@angular/platform-browser';


@Component({
  selector: 'app-music-detail',
  templateUrl: 'music-detail.component.html',
  styleUrls: ['music-detail.component.css'],
  providers: [MusicService]
})

export class MusicDetailComponent implements OnInit {
  public album: Album;
  public showSideDrawer: boolean = false;
  public trackList: Audio[];
  public embedList: Embed[];
  public selectedAudio: Audio;
  @Input() public albumId:string;
  @Input() chooseTemplate;
  public visibleEmbed : string;
  public selectedIndex :number = 0;
  public callCount: number = 0;
  initDone:boolean = false;

   constructor(private _musicService: MusicService,
       private route: ActivatedRoute,private sanitizer: DomSanitizer) {}

  ngOnInit(): void {
      this.callAlbumDetail();
      this.initDone= true;
  }

  callAlbumDetail(){
    this._musicService.getAlbumDetail(this.albumId).subscribe(
        data => {
          this.album = data[0];
          this.album.albumArt = Utils.getImageSrc(this.album.albumArt);
          if(this.albumId.length < 1){
            this.albumId = this.album.nid;
          }
          this.callEmbed();
        },
        err => console.error(err),
        () => console.log('done')
    );
    window.scroll(0, 0);
  }

  toggleSideDrawer() {
      this.callCount = this.callCount + 1;
      this.assignAlbumToAudio(this.album);
  }

  assignAlbumToAudio(album) {
     let temp = {
        title: album.title,
        body: "",
        albumArt: album.albumArt,
        nid: album.nid,
        path: album.path,
        albumName: album.title,
        spotifyTrackID: "",
        source: "",
        isPlaying: false,
        albumLink: album.path,
        field_download_links: "",
        field_streaming_links: "",
      }
    if (album && typeof album.field_download_links !== "object" && album.field_download_links !== "") {
        temp.field_download_links =  album.field_download_links;
        temp.field_streaming_links =  album.field_streaming_links;
    } 
    this.selectedAudio = <Audio>(temp);
  }

  sideDrawerClose() {
    this.showSideDrawer = false;
  }

  callEmbed(): void {   
    
    console.log("this.album");
    console.log(this.album);
      this._musicService.getAudioList(this.albumId).subscribe(
        data => {
          this.trackList = data;
        }
      );

      this._musicService.getAlbumEmdeds(this.albumId).subscribe(
      data => {
        this.embedList = data;
        this.visibleEmbed =this.embedList[0].service;
        for(let x =0; x < this.embedList.length;x++){
          this.embedList[x].safeHtml = this.sanitizer.bypassSecurityTrustHtml(this.embedList[x].embed.toString());
        }
        console.log("this.embedList----------------------");
        console.log(this.embedList);
      }
    );    
  }

  displayEmbed(list:Embed,i) {
    this.visibleEmbed = list.service;
    this.selectedIndex = i;
  }
  
  ngOnChanges(): void {
    if(this.initDone){
      this.callAlbumDetail();
    }
  }

}


