import { Component, Input, OnInit, OnChanges } from '@angular/core';
import {Tour, TourService} from '../tour.service';
import { TourSearch } from '../tour-search.pipe';
import { GeneralService } from '../../general.service';
import { Utils } from '../../utils';

@Component({
  selector: 'app-tour-block',
  templateUrl: 'tour-block.component.html',
  styleUrls: ['tour-block.component.css'],
  providers: [TourService]
})
export class TourBlockComponent implements OnInit, OnChanges {
  @Input() count;
  @Input() enableNoResult:boolean = false;
  @Input() nearDates:boolean = false;
  @Input() isUpcoming:boolean = true;
  @Input() searchKey:string = "";
  countryKey:string = "";
  matchLocation: any;
  geoLocationInfo:any;
  public tour: Tour[];
  empty = false;
  mapImageURL:string;

  constructor(private _tourService: TourService, private _navigationSource: GeneralService){
    this.matchLocation = Utils.loacationObj;
    localStorage.removeItem(this.isUpcoming ? "upcomingdates" : "pastdate");
    _navigationSource.navigationSource$.subscribe(
      data => {
        console.log("----------------------------geoLocationInfo--------------------------------------");
        console.log(data);
        this.matchLocation = data;
        //this.calculateDistance();
        this.getCurrentLocationMap();
      });
  }

  getTour(){

    
    if(localStorage){
      this.tour = JSON.parse(localStorage.getItem(this.isUpcoming ? "upcomingdates" : "pastdate"));
      console.log(this.tour);
    }

    if(this.tour && this.tour.length > 1){
      //if(this.isUpcoming) this.calculateDistance();
      return this.tour;
    }
    
    this._tourService.getTourContent(!this.isUpcoming).subscribe(
        data => {
          this.tour = data;
          if(data.length == 0)this.empty = true;
          //if(this.isUpcoming) this.calculateDistance();
          console.log("----------------------------this.tour = data-----------------------------------");
          console.log(this.tour);
          if(localStorage) localStorage.setItem(this.isUpcoming ? "upcomingdates" : "pastdate", JSON.stringify(this.tour));
        },
        err => console.error( err),
        () => console.log('done')
    );   

    return this.tour;
  }

  calculateDistance(){
    let counter = 0;
    while(true && this.matchLocation){
      if(this.tour[counter] && !this.tour[counter]["distance"]){
        let distance = this.findShortDistance(this.tour[counter]);
        //console.log("----distance----"+distance);
        this.tour[counter]["distance"] = distance;
        counter++;
      }else{
        //counter = counter - 1;
        break;
      }
    }
  }

  toRadians(degrees){
    let pi = Math.PI;
    return degrees * (pi/180);
  }

  findShortDistance_old(tourN: Tour){
    let lat2 = Number(tourN["latitude"]);
    let long2 = Number(tourN["longitude"]);
    let R = 6371 * 1000; // metres
    let φ1 = this.toRadians(this.matchLocation["latitude"]);
    let φ2 = this.toRadians(this.matchLocation["longitude"]);
    let Δφ = this.toRadians(lat2 - this.matchLocation["latitude"]);
    let Δλ = this.toRadians(long2 - this.matchLocation["longitude"]);

    let a = Math.sin(Δφ/2) * Math.sin(Δφ/2) + Math.cos(φ1) * Math.cos(φ2) * Math.sin(Δλ/2) * Math.sin(Δλ/2);
    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));

    return (R * c)/1000;
  }

  findShortDistance(tourN: Tour){
    let lat1 = Number(tourN["latitude"]);
    let lon1 = Number(tourN["longitude"]);

    let lat2 = this.matchLocation["latitude"];
    let lon2 = this.matchLocation["longitude"];

    var φ1 = this.toRadians(lat1), φ2 = this.toRadians(lat2), Δλ = this.toRadians(lon2-lon1), R = 6371e3; // gives d in metres
    var d = Math.acos( Math.sin(φ1)*Math.sin(φ2) + Math.cos(φ1)*Math.cos(φ2) * Math.cos(Δλ) ) * R;

    return d/1000;
  }

  ngOnChanges() {
    if(!this.enableNoResult){
      this.getTour();
      if(this.isUpcoming && this.nearDates) this.getCurrentLocationMap();
    }else{
        //this.tour = [];
        this.empty = true;
    }
  }

  ngOnInit() {
  }

  getZoomLevel(radius:number):number {
    let scale:number = radius / 500;
    let zoomLevel = Math.round(16 - Math.log(scale) / Math.log(2));
    return zoomLevel;
  }

  ngAfterViewInit() {
    
  }

  getCurrentLocationMap(){
    
    if(!this.mapImageURL && this.matchLocation){
      
      let location = 'https://maps.googleapis.com/maps/api/staticmap?size=640x640&center=';
      location = location + this.matchLocation["latitude"]+','+this.matchLocation["longitude"];
      location = location + "&style=feature:transit.line|visibility:simplified|color:0xbababa&style=feature:road.highway|element:labels.text.stroke|visibility:on|color:0xb06eba&style=feature:road.highway|element:labels.text.fill|visibility:on|color:0xffffff";
      //tour.filter()
      let ftour = undefined;
      if(this.tour){
        ftour = this.tour.filter(t=> t["distance"] && t["distance"] <= Number(160));
        for(let i=0;i<ftour.length;i++){
          location = location + "&markers=color:red|label:E|"+ftour[i]["latitude"] + ',' + ftour[i]["longitude"];
        }
      }
      if(ftour && ftour.length > 0) location = location + "&zoom=9";
      else location = location + "&zoom=13";
      location = location + "&markers=color:green|label:M|"+this.matchLocation["latitude"]+','+this.matchLocation["longitude"];
      location = location + "&key=AIzaSyC0WZPxqif5jYsN02nPGEFyWIKLzYMIfmQ";
      this.mapImageURL = location;
      //console.log("----------------------------getCurrentLocationMap--------------------------------------");
      //console.log(this.mapImageURL);
    }
  }
}


