import { Component, OnInit, ElementRef,HostListener,AfterViewInit,ViewChild } from '@angular/core';
import { Routes, Router, NavigationEnd, NavigationStart, NavigationCancel } from '@angular/router';
import { delayedButtonFade } from './animations/animations';
import { Title } from '@angular/platform-browser';
import { environment } from '../environments/environment';
import { Angulartics2,Angulartics2On } from 'angulartics2';
import { Angulartics2Omniture } from './angulartics-omniture/angulartics-omniture';
import { Utils } from './utils';
import { GeneralService } from './general.service';
import { ArtistService } from "./artists/artists.services";
import { PerfectScrollbarModule } from 'angular2-perfect-scrollbar';
import { PerfectScrollbarConfigInterface } from 'angular2-perfect-scrollbar';
const PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true
};



@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [delayedButtonFade],
  providers:  [GeneralService],
  host: {
    '(window:resize)': 'onResize($event)'
  }
})
export class AppComponent implements OnInit {
  public isMobileMenu:boolean = false;
  public showMenu:boolean = false;
  public breadcrumb = [];
  private shouldStick:boolean =false;
  public isMlistshow:boolean=false;
  private mobilereolution: boolean = false;
    private desktopResolution: boolean = false;
  
  
  constructor(private router: Router, private titleService: Title, private el:ElementRef,
    angulartics2: Angulartics2, angulartics2Omniture: Angulartics2Omniture, private _generalService: GeneralService) {
    /*
    router.events.forEach((event) => {
      if(event instanceof NavigationStart) {
        //console.log("-----------------NavigationStart----------------------------------");
        console.log(event);
      }
      if(event instanceof NavigationCancel) {
        console.log("-----------------NavigationCancel----------------------------------");
        console.log(event);
      }
      // NavigationEnd
      // NavigationCancel
      // NavigationError
      // RoutesRecognized
    });*/

  }

  env = environment;

  @HostListener('window:scroll', ['$event'])
  makeNavSticky(event){
    let scrollTop = event.pageY | document.body.scrollTop;
    let heightDecider;
    if(this.isMobileMenu){
      heightDecider = 50;
    }else{
      heightDecider = 10;
    }
    if(scrollTop > (window.innerHeight - heightDecider)){
      this.shouldStick = true;
    }else{
      this.shouldStick = false;
    }
  } 


  ngOnInit() {
      window.scroll(0, 0);
     

      if(window.screen.width < 1023){
          this.mobilereolution = true;
          this.desktopResolution = false;
      }
      if(window.screen.width > 1023){
          this.desktopResolution = true;
          this.mobilereolution = false;
      }


    if(Utils.checkMobile()){
      this.isMobileMenu = true;
    } 

    if(navigator.geolocation){
      navigator.geolocation.getCurrentPosition(this.setPosition.bind(this));
    };
  }
    ngAfterViewInit() {
        this.toggleClass(document.getElementsByTagName('body')[0], 'loading');
        document.body.classList.remove("loading");  
        console.info("This is from ngAfterInit");
     }

  setPosition(position){
    console.log(position.coords);
    this._generalService.geolocate(false, false, "visitorGeolocation").subscribe(
        data => {
          
          //console.log(data);

          let location = {}
          location["latitude"] = position.coords.latitude;
          location["longitude"] = position.coords.longitude;
          location["country"] = data.countryName;
          this._generalService.tellNavigationSource(location);
          console.log("------------------this._generalService.tellNavigationSource--------------------");
          //if(localStorage) localStorage.setItem("Clocation", JSON.stringify(location));
        },
        err => console.error( err),
        () => console.log('done')
    );
    console.log(position.coords);
  }


  public setTitle(newTitle: string) {
   
    this.toggleMenu();
    if(newTitle=="NewsLetter"){
      this.mailinglistcall();
    }
    else{
       this.titleService.setTitle(newTitle);
    }
    
  }
  mailinglistcall(){

    this.isMlistshow=true;
   
    
  }
  closelightbox( ){
   
     this.isMlistshow=false;
  }
  toggleMenu(){
    if(this.isMobileMenu){
      this.showMenu = !this.showMenu;
    }
  }

  onResize(event){
    if(event.target.innerWidth < 767){
      this.isMobileMenu = true;
    }else{
      this.isMobileMenu = this.showMenu = false;
    }
  }
    onResizefot(event) {
      if(event.target.innerWidth < 1023){
          this.mobilereolution = true;
          this.desktopResolution = false;
      }
      if(event.target.innerWidth > 1023){
          this.desktopResolution = true;
          this.mobilereolution = false;
      } 
  }

       toggleClass(ele, class1) {
        let classes = ele.className;
        let regex = new RegExp('\\b' + class1 + '\\b');
        let hasOne = classes.match(regex);
        class1 = class1.replace(/\s+/g, '');
        if (hasOne)
          ele.className = classes.replace(regex, '');
        // else
        //   ele.className = classes + class1;
      }
      
}




