import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'promotedToFrontpage'
})
export class promotedToFrontpage implements PipeTransform {
  transform(value: any): any {
    let filter = "On".toLocaleLowerCase();
   
      return filter ? value.filter(t=> t.promote.toLocaleLowerCase().indexOf(filter) != -1) : value;      
   
   
  }
}


