import { Injectable } from '@angular/core';
import { Jsonp, Response } from '@angular/http';
import {Observable} from 'rxjs/Rx';
import { environment } from '../../environments/environment';

import {Tour, TourService} from './tour.service';

@Injectable()
export class TourStorage {

  public tourCache:any = {
    "upcomingdates" : null
  };

  constructor(private jsonp: Jsonp) { }

  saveTourDataInCache(key:string, data:Tour[]){
    this.tourCache[key] = data;
  }

  getTourDataFromCache(key:string){
    return this.tourCache;
  }

  isStorageIsEmpty(key:string):boolean{
    console.log(key+"---------isStorageIsEmpty-----------");
    console.log((!this.tourCache[key])+" || "+(this.tourCache[key] == null));
    return !this.tourCache[key] || this.tourCache[key] == null;
  }

}


